import React, { useState, useEffect } from 'react';
import { Wrapper } from './styles';

export const Gallery = () => {
    const [workingArray, setWorkingArray] = useState();

    const [query, setQuery] = useState('');

    let exampleRequest = `https://images-api.nasa.gov/search?q=${query}&media_type=image`;
    if (query === '') {
        exampleRequest = `https://images-api.nasa.gov/search?&media_type=image`;
    }

    const getSearch = async () => {
        try {
            const response = await fetch(exampleRequest);
            const data = await response.json();

            const newArray = [];
            if (data.collection.items === []) {
                setWorkingArray(false);
                console.log('empty array');
            }
            else {
                for (let i = 0; i < 100; i++) {
                    newArray[i] = data.collection.items[i];
                }
                setWorkingArray(newArray);
                console.log('filled array');
            }
            
        } catch {
            console.log("error");
            setWorkingArray(false);
        }
    }

    const goSearch = (e) => {
        e.preventDefault();
        getSearch();
        console.log(workingArray);
    }


    useEffect(() => {
        getSearch();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])

    const updateQuery = (e) => {
        setQuery(e.target.value);
    }

    const renderImages = () => {
        console.log(workingArray);
        if (workingArray) {
            workingArray.map((element, index) => {
                return <img key={workingArray[index].data[0].nasa_id} src={workingArray[index].links[0].href} alt="img" />
            })
        } else {
            <h1>errorrrr</h1>
        }
    }

    return (
        <Wrapper>
            <form onSubmit={goSearch}>
                <div>
                    <input id="queryInput" type="text" onChange={updateQuery} />
                    <button>submit</button>
                </div>
                <div>
                    {renderImages()}
                </div>
            </form>
        </Wrapper>
    )
}
